import React, { Component } from "react";
import { motion } from "framer-motion";

class Card extends Component {
    render() {
        const target = this.props.project.link == "javascript:void(0);" ? "" : "_blank"; 
        return (
            <motion.div className="card-container">
                <div className="card-header text-header">
                    <h2 className="scale-text-header">{  this.props.project.header  }</h2>
                </div>
                <div className="card-wrapper">
                    <div className="card-content">
                        <img className="card-img" src={this.props.project.img}/>
                        <a className="card-link" href={  this.props.project.link  } target={target}>
                            <motion.p className="scale-text-body text-body" transition={{ duration: .2 }} whileHover={{  backgroundColor: "#3a3a3a"  }}>- link to project -</motion.p>
                        </a>
                        <div className="card-text-content scale-text-body text-body" >
                            <p dangerouslySetInnerHTML={{  __html: this.props.project.text  }}></p>
                        </div>
                        <p className="card-footer scale-text-body">{  this.props.project.year  }</p>
                    </div>
                </div>
            </motion.div>
        )
    }
}

export default Card;