import React, { Component } from "react";
import { motion } from "framer-motion";

class BannerContent extends Component {
    render() {
        return (
            <motion.div className="banner-motion" id={this.props.id}
            initial={{
                x: (this.props.offset*100)+'vw',
            }}
            animate={{
                x: (this.props.position*100)+'vw',
            }}
            transition={{ type: "spring", duration: this.props.duration }}>
                <div className="banner-img-wrapper">
                    <img className="banner-content-img" src={  this.props.project.img  }/>
                </div>
                <div className="banner-text-wrapper">
                    <div className="banner-content-text">
                        <h2 className="banner-header scale-text-header text-header">{  this.props.project.header  }</h2>
                        <p className="scale-text-body text-body" dangerouslySetInnerHTML={{  __html: this.props.project.text  }}></p>
                        <a className="banner-project-link scale-text-body text-body" href={  this.props.project.link  } target="_blank">- link to project -</a>
                    </div>
                </div>
            </motion.div>
        )
    }
}

class Banner extends Component {
    constructor(props) {
        super(props);
        this.state = {
            curProject: 1,
            position: 0,
            duration: 0,
            animating: false,
        }
    }

    slideLeft = () => {
        if(this.state.animating) return;

        this.setState({
            position: 1,
            duration: .7,
            animating: true,
        });

        setTimeout(() => {
            this.setState({
                curProject: this.projectIndex(this.state.curProject - 1),
                duration: 0,
                position: 0,
                animating: false,
            });
        }, 750);
    }

    slideRight = () => {
        if(this.state.animating) return;

        this.setState({
            position: -1,
            duration: .7,
            animating: true,
        });

        setTimeout(() => {
            this.setState({
                curProject: this.projectIndex(this.state.curProject + 1),
                duration: 0,
                position: 0,
                animating: false,
            });
        }, 750);
    }

    static positionTemplates(pos) {
        return 'translateX('+(pos*100)+'vw)';
    }

    projectIndex(idx) {
        if(idx < 0)
            return this.props.projects.length - 1;
        else if(idx >= this.props.projects.length)
            return 0;
        return idx;
    }

    render() {
        return (
            <div className="banner-wrapper">
                <h2 className="banner-tag content-container">Featured:</h2>
                <div className="banner-skew-div">
                    <div className="content-container">
                        <motion.div className="banner-button" onClick={this.slideLeft} 
                        animate={{ opacity: this.state.animating? .6 : 1 }}>
                            <svg className="banner-svg" viewBox="0 0 30 100" > 
                                <path d="M 25 5 L 5 50 L 25 95" />
                            </svg>
                        </motion.div>
                        <div className="banner-content-wrapper">
                            <BannerContent project={this.props.projects[this.projectIndex(this.state.curProject-1)]}
                                position={this.state.position-1} offset={-1} duration={this.state.duration}/>
                            <BannerContent project={this.props.projects[this.projectIndex(this.state.curProject)]}
                                position={this.state.position} offset={0} duration={this.state.duration}/>
                            <BannerContent project={this.props.projects[this.projectIndex(this.state.curProject+1)]}
                                position={this.state.position+1} offset={1} duration={this.state.duration}/>
                        </div>
                        <motion.div className="banner-button" onClick={this.slideRight}
                        animate={{ opacity: this.state.animating? .6 : 1 }}>
                            <svg className="banner-svg" viewBox="0 0 30 100" > 
                                <path d="M 5 5 L 25 50 L 5 95" />
                            </svg>
                        </motion.div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Banner;